import React,{useState} from 'react'
import {
    BrowserRouter as Router,
    Route,
    Switch,
    Link
  } from 'react-router-dom'
import './Style/Header.css'
function Header(props){
    return (
        <Router>
        <div className="Header">
            <div className="title">Introduction2</div>            
            <div className="router wrapper">
                <div className="router content">
                    <Link to={'/home'} onClick={()=>{props.k('/home')}}>Home</Link>
                </div>
                <div className="router content">
                    <Link to={'/blog'} onClick={()=>{props.k('/blog')}}>Blog</Link>
                </div>
                <div className="router content">
                    <Link to={'/gallery'} onClick={()=>{props.k('/gallery')}}>Gallery</Link>
                </div>
                <div className="router content">
                    <a href="https://github.com/kimtahen">GitHub</a>
                </div>
            </div>
        </div>
        </Router>
    )
}
export default Header;